
.menu-list-left-container{
	margin: 0;
	padding:0;
	list-style: none;
	text-align: left;
	line-height: 40px;
	border: 1px solid #e7e7e7;
	background-color: #F8F8F8;
	font-size: 14px;
	box-sizing: border-box;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	color: #6C6C6C;
}



