
.menu-list-item{
	border-bottom: 1px solid #e7e7e7;
	text-indent: 10px;
	position: relative;
	right: 0;
}
.menu-list-item:last-of-type{
	border-bottom: none;
}
.menu-list-item>a{
	text-decoration: none;
	color: #6C6C6C;
	display: block;
}
.item-add-icon:after{
	content: '+';
	position: absolute;
	top: 0;
	right: 10px;
	width:20px;
	text-align: center;
	font-weight: bold;
	font-size:18px;
}
.item-plus-icon:after{
	content: '-';
	position: absolute;
	top: 0;
	right: 10px;
	width:20px;
	text-align: center;
	font-weight: bold;
	font-size:18px;
}
.menu-list-item-child-container{
	margin:0;
	padding:0;
	list-style: none;
	font-size: 12px;
}
.menu-list-item-child-container .menu-list-item{
	padding:0 0 0 5px;
	background: #fff;
}
.menu-list-item-child-container .menu-list-item:first-child{
	border-top: 1px solid #e7e7e7;
}



