
.panel{
            width:400px;
            height:240px;
            border:1px solid #ccc;
            position:relative;
}
.panel .title{
            background-color:#eee;
            text-align:center;
            line-height:30px;
            border:1px solid #fff;
            font-weight:bold;
}
.ui-Resizable-r{
            position:absolute;
            right:0;
            top:0;
            width:10px;
            height:100%;
            /*background-color:green;*/
            cursor:e-resize;
}
.ui-Resizable-b{
            position:absolute;
            right:0;
            bottom:0;
            width:100%;
            height:10px;
            /*background-color:blue;*/
            cursor:s-resize;
}
.ui-Resizable-rb{
            position:absolute;
            right:0;
            bottom:0;
            width:20px;
            height:20px;
            /*background-color:red;*/
            cursor:nw-resize;
}



