
#mask{
            background:#000;
            opacity:0.75;
            filter:alpha(opacity=75);
            height:1000px;
            width:100%;
            position:absolute;
            left:0;
            top:0;
            z-index:1000;
}
#login{
            position:fixed;
            left:30%;
            top:30%;
            z-index:1001;
}
.loginCon{
            width:670px;
            height:380px;
            background:url(img/loginBg.png) no-repeat;
            position: relative;
}
#close{
            width:30px;
            height:30px;
            background:url(img/close.png) no-repeat;
            cursor: pointer;
            position:absolute;
            right:5px;
            top:5px;
}



